@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@500&display=swap);
*{padding:0;margin:0;box-sizing:border-box;font-family:"Baloo Paaji 2", monospace}body{background:#00445e}body header{max-width:1024px;margin:0 auto;padding:1rem 2rem}body header .header-container{display:flex;justify-content:space-between;align-items:center;color:#c5c5c5}body header .header-container h1{font-size:2.2rem}body header .header-container .options{list-style:none;display:flex}body header .header-container .options li{padding:0.4rem;background-color:#c5c5c5;border:1px solid #00445e;display:flex;border-radius:50%;margin-left:0.4rem;position:relative;cursor:pointer}body header .header-container .options li:before{content:"";position:absolute;width:100%;height:100%;top:0;left:0;background-color:#00445e;border-radius:50%;transition:all 0.2s linear;-webkit-transform:scale(0);transform:scale(0);opacity:0}body header .header-container .options li:hover:before{-webkit-transform:scale(1);transform:scale(1);opacity:1;border:1px solid #c5c5c5}body header .header-container .options li:hover a{color:#c5c5c5}body header .header-container .options li a{color:#00445e;font-size:1.2rem;display:flex;justify-content:center;align-items:center;transition:all 0.2s linear;z-index:0}body main{max-width:1030px;margin:0 auto;padding:0 2rem}body main .information{background-color:rgba(0,255,255,0.274);border-radius:0.3rem 0.3rem 0 0;width:100%}body main .information ul{list-style:none;padding:0.5rem 0 0 0.5rem;display:flex}body main .information ul li{margin-right:1rem;color:whitesmoke}body main .container{width:100%;height:30rem;border-radius:0 0 0.3rem 0.3rem;background-color:rgba(0,255,255,0.274);padding:0.5rem}body main .container .game-wrapper{width:100%;height:100%;overflow-y:scroll;overflow-x:scroll}body footer{max-width:1024px;margin:0 auto;padding:1rem 2rem}body footer .footer-container{display:flex;justify-content:center;align-items:center;flex-direction:column;color:#c5c5c5}body footer .footer-container h3{margin-bottom:0.5rem}body footer .footer-container .options{list-style:none;display:flex;justify-content:center;align-items:center}body footer .footer-container .options a{margin:0 0.3rem;color:#c5c5c5;display:flex;justify-content:center;align-items:center;font-size:1.8rem;cursor:pointer;transition:0.5s all linear}body footer .footer-container .options a:hover{-webkit-transform:rotate(360deg) scale(0.8);transform:rotate(360deg) scale(0.8)}@media (max-width: 640px){.header-container{flex-direction:column}}

